@import url(https://fonts.googleapis.com/css?family=Arbutus|Pacifico|Roboto);
.App {
  text-align: center;
}

.login {
  margin-top: 5em;
  /* padding: 20px; */
  width: 100vw;
  height: 100vh;
  /* font-size: 3em;   */
}

.logo {
	height: 30vh;
}

.form {
  padding: 20px;
  width: 100vw;
  height: 20vh;
  font-size: 3em;
}

.login-button {
  font-size: .8em;
  margin-top: 1em;
}

.font-s {
  font-size: .5em;
}
.section-menu {
	margin-top: 50px;
}
.nav-header {
  background-color: #282c34;
  font-size: calc(8px + 2.5vmin);
  color: white;
}

.nav-logo {
  margin-top: .3em;
	height: 2em;
}

.nav-margin{
  display: flex;
}

.nav-button {
  margin: .5em;
}

.font-nav {
  display: flex;
  font-size: .8em;
}

@media screen and (max-width: 600px) {
  .nav-button {
    font-size: .1em;
    margin: .4em;
    border-radius: 50%;
  }
  .font-nav {
    font-size: .6em;
  }
}

.margin-button-menu {
  margin: 1em;
}
.height-c-command {
	text-align: left;
	padding: 0;
	width: 100%;
	height: 4em;
	display: flex;
	font-size: 1.2em;
}

.total {
	font-size: 1.3em;
}

.delte-item {
	margin-top: .4em;
	padding: 1px;
	height: 1.5em;
	width: 15%;
	border-radius: 50% !important;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #444444;
  color: rgb(223, 223, 226);

}

code {
  font-family: 'Raleway', sans-serif;
}

.font-title {
  font-family: 'Arbutus', cursive;
}

.font-queen {
  font-family: 'Pacifico', cursive;
}

.btn, .btn-large {
  border-radius: 5px;
  padding: 0 .5rem;
  font-size: calc(8px + 2.5vmin);
}



